import * as React from 'react';
import {Link} from 'gatsby';
import loadable from '@loadable/component';

import '../styles/home/weekly-recipe-card.scss';

const Arroz = loadable(() => import('../arroz-con-webo'));

export default function WeeklyRecipeCard({data}) {
    const [image, set_image] = React.useState("");
    React.useEffect(() => {
        (async () => {
            set_image((await import(`../images/${data?.image}`)).default);
        })();
    }, [data])

    return (
        <Link to="/recipe/" state={{'recipe_clicked': data?.pk}}>
            <Arroz.FilledCard className="weekly-recipe-card" role="primary" containerType="container" rounded interactable>
                <Arroz.Image className="weekly-recipe-image" source={image} />
                <div className="weekly-recipe-info">
                    <Arroz.Title>Weekly Recipe!</Arroz.Title>
                    <Arroz.Subheading>{data?.name}</Arroz.Subheading>
                    <Arroz.Label role="primary" pill>{data?.culture}</Arroz.Label>
                    <Arroz.Body>{data?.desc}</Arroz.Body>
                </div>
            </Arroz.FilledCard>
        </Link>
    );
}