import * as React from 'react';
import loadable from '@loadable/component';

import '../styles/home/news-letter-subscribe.scss';

const Arroz = loadable(() => import('../arroz-con-webo'));

export default function NewsLetterSubscribe({}) {
    const handleSubmit = event => {

    };

    return (
        <Arroz.FilledCard className="news-letter-subscribe">
            <Arroz.Title>Subscribe to our newsletter!</Arroz.Title>
            <Arroz.Subheading>Get culture to your inbox daily!</Arroz.Subheading>
            <Arroz.Form className="subscribe-form" onSubmit={handleSubmit}>
                <Arroz.FilledTextBox className="subscribe-textbox" label="Your email">example@email.com</Arroz.FilledTextBox>
                <Arroz.FilledButton pill type="submit">Submit</Arroz.FilledButton>
            </Arroz.Form>
        </Arroz.FilledCard>
    );
}